import vue from 'vue';
import vuex from 'vuex';

import axios from 'axios';
import router from '../router/index';
import authStore from './auth-store';

vue.use(vuex);

export default new vuex.Store({
  state: {
    user: {}
  },
  modules: {
    authStore
  },
  mutations: {
    setUser(state, payload) {
      state.user = payload;
    },
    clearData(state, payload) {
      state.user = {};
    }
  },
  actions: {}
});



