import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Login_SignUp from '@/components/Login_SignUp'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/login',
      name: 'Login_SignUp',
      component: Login_SignUp
    }
  ]
})



